<template>
  <div class="ad" v-drag>
    <div :value="1" class="item">
      <v-menu :close-on-content-click="true" :nudge-width="200" offset-y>
        <!-- 下拉菜单 -->
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            fab
            large
            elevation="12"
            color="rgba(0,0,0,0.1)"
            dark
            v-bind="attrs"
            v-on="on"
          >
            <v-icon x-large> mdi-plus </v-icon>
          </v-btn>
        </template>

        <v-card>
          <!--菜单列表-->
          <v-list nav dense>
            <v-list-item text link @click="open('Markdown')">
              <!--Markdown入口-->
              <v-list-item-title>New Markdown </v-list-item-title>
            </v-list-item>

            <v-list-item link @click="open('Notetxt')">
              <!--文字说明-->
              <v-list-item-title>New Easynote </v-list-item-title>
            </v-list-item>

            <v-list-item link @click="open('NoteECY')">
              <!--文字说明-->
              <v-list-item-title>New ACGnote </v-list-item-title>
            </v-list-item>

            <v-list-item link @click="open('VideoPlayer')">
              <!--文字说明-->
              <v-list-item-title>Video </v-list-item-title>
            </v-list-item>
          </v-list>
        </v-card>
      </v-menu>
    </div>
  </div>
</template>

<script>
export default {
  name: "homeDragbtn",
  directives: {
    drag: {
      inserted(el) {
        let [x, y, l, t, isDown] = [0, 0, 0, 0, false];
        el.onmousedown = (e) => {
          //获取x坐标和y坐标
          x = e.clientX;
          y = e.clientY;
          //获取左部和顶部的偏移量
          l = el.offsetLeft;
          t = el.offsetTop;
          //开关打开
          isDown = true;
          //设置样式
          el.style.cursor = "move";
        };
        //鼠标移动
        el.onmousemove = (e) => {
          if (isDown == false) return;
          //获取x和y
          let [nx, ny] = [e.clientX, e.clientY];
          //计算移动后的左偏移量和顶部的偏移量
          let nl = nx - (x - l);
          let nt = ny - (y - t);
          el.style.left = nl + "px";
          el.style.top = nt + "px";
        };
        //鼠标抬起事件
        el.onmouseup = () => {
          //开关关闭
          isDown = false;
          el.style.cursor = "default";
        };
      },
    },
  },
  methods: {
   
    // 通过视图的名字跳转到特定视图，一般用作主页，登录页，注册页等
    open(name) {
      // 需要先判断当前路由名称是否和目标路由一致，如果一致就不跳转
      if (this.$route.name !== name) {
        this.$router.push({
          name: name,
        });
      }
    },
  },
};
</script>

<style>
.icon {
  font-size: 40px;
}
.ad {
  position: fixed;
  right: 20px;
  bottom: 30%;
  z-index: 9999999;
}
</style>
