<template>
  <!--v-app，有且只有一个-->

  <v-app id="app">
    <!--导航侧栏，drawer用于侧栏的显示和隐藏-->
    <side-drawer> </side-drawer>

    <!--顶部导航栏，clipped-left用于控制导航侧栏在左边-->
    <head-drawer>
      
    </head-drawer>

    <!--页面内容部分，用视图替换体现-->
    <v-main
      v-bind:style="{
        backgroundImage: 'url(' + bg + ')',
        backgroundRepeat: 'repeat',
        backgroundSize: '100% 100%',
      }"
    >
      <router-view></router-view>
      <!--修改index.js文件，可以进行视图的替换-->
      
    </v-main>

    <!--最下方的底部导航栏-->
    <v-bottom-navigation app color="primary"> 备案 </v-bottom-navigation>
    
    <home-dragbtn> </home-dragbtn>
  </v-app>
</template>

<script>
import HeadDrawer from "./views/conponents/HeadDrawer.vue";
import homeDragbtn from "./views/conponents/homeDragbtn.vue";
import Ratecard from "./views/conponents/Ratecard.vue";
import SideDrawer from "./views/conponents/SideDrawer.vue";
import Messagecard from "./views/conponents/Messagecard.vue";


export default {
   created() {
    //在页面加载时读取sessionStorage里的状态信息
    if (sessionStorage.getItem('store')) {
      this.$store.replaceState(Object.assign({}, this.$store.state, JSON.parse(sessionStorage.getItem('store'))));
    }
    //在页面刷新时将vuex里的信息保存到sessionStorage里
    window.addEventListener('beforeunload', () => {
      sessionStorage.setItem('store', JSON.stringify(this.$store.state));
    });
  },
  components: { homeDragbtn, Ratecard, SideDrawer, HeadDrawer,Messagecard },
  name: "App",

  data: () => ({
    // 控制导航侧栏的显示和隐藏

    bg: require("@/assets/background/pic/23.jpg"),
  }),

  methods: {
    // 通过视图的名字跳转到特定视图，一般用作主页，登录页，注册页等
    open(name) {
      // 需要先判断当前路由名称是否和目标路由一致，如果一致就不跳转
      if (this.$route.name !== name) {
        this.$router.push({
          name: name,
        });
      }
    },
  },
};
</script>
<style lang="scss">
  @import "./assets/reset.css";
  body{
    font-family: xj;
  }
</style>
