import Vue from 'vue'
import App from './App.vue'
import router from './router'
import vuetify from './plugins/vuetify'
import mavonEditor from 'mavon-editor'
import homeDragbtn from './views/conponents/homeDragbtn.vue'
import rateCard from './views/conponents/Ratecard.vue'
import SideDrawer from './views/conponents/SideDrawer.vue'
import HeadDrawer from './views/conponents/HeadDrawer.vue'
import VideoPlayer from 'vue-video-player'
import axios from 'axios'
import VueAxios from 'vue-axios'
import store from './store'

require('video.js/dist/video-js.css')
require('vue-video-player/src/custom-theme.css')

Vue.use(VideoPlayer)
Vue.use(VueAxios, axios)
Vue.use(mavonEditor)



Vue.config.productionTip = false

new Vue({
  router,
  vuetify,
  render: h => h(App),
  store,
  homeDragbtn,
  rateCard,
  SideDrawer,
  HeadDrawer,
  components: { App },
  template: '<App/>',
}).$mount('#app')
