  > <!-- src="../../assets/background/pic/26.jpg" -->
    <!--左上方的头像、用户名和邮箱的显示-->
   <v-list>
        <v-list-item class="px-2">
          <v-list-item-avatar>
            <v-img
              v-if="this.$store.state.user.icon"
              src="$store.state.user.icon"
              alt="头像"
            ></v-img>
            <v-img v-else src="../../assets/user-icon.jpeg" alt="头像"></v-img>
          </v-list-item-avatar>
