          <v-avatar slot="icon" color="black" size="40">
            <v-icon icon="mdi-lock" color="white">mdi-lock</v-icon>
          </v-avatar>
          <!--文字介绍-->
          首页
          <template v-slot:actions>
            <v-btn text color="black" @click="open('Login')">Login</v-btn>
            <v-btn text color="black" @click="open('Register')">Register</v-btn>
          </template>
        </v-banner>
      </v-col>
    </v-row>
    <!--信息卡片-->
    <v-row justify="center" class="mt-6">
