<script>
export default {
  name: "Home",
  data: () => ({
    storeEnabled: 0,
  }),
  computed: {
    isFollow() {
      return this.$store.state.user.enabled; //需要监听的数据
    },
  },
  watch: {
    isFollow(newVal, oldVal) {
      //do something
      console.log(newVal);
      console.log(oldVal);
    },
  },
  methods: {
    // 通过视图的名字跳转到特定视图，一般用作主页，登录页，注册页等
    open(name) {
      // 需要先判断当前路由名称是否和目标路由一致，如果一致就不跳转
      if (this.$route.name !== name) {
        this.$router.push({
          name: name,
        });
      }
    },
  },
};
</script>
